<template>
  <div class="home_page" id="home_page">
    <div class="text_part" id="text_part">
      <div class="idea_box" id="idea_box">
        <div class="idea_bg" id="idea_bg"></div>
        <div
          class="idea_letter"
          id="idea_letter"
          v-for="item in idea"
          :key="item.idx"
          :style="{ visibility: item.visibility }"
        >
          {{ item.letter }}
        </div>
      </div>

      <div class="check_box" id="check_box">
        {{ global.lang_pack.check }}
      </div>
    </div>

    <div class="guide_part" id="guide_part">
      <div class="guide_box" id="guide_box">
        <div class="guide_ball" id="guide_ball"></div>
      </div>
    </div>
    <Logo></Logo>
  </div>
</template>

<style lang="css">

body {
  background: #f7f7f7;
}

div {
  font-family: FuturaLT;
  color: #251714;
}

::-webkit-scrollbar {
  display: none;
}

div.home_page {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

div.text_part {
  position: fixed;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

div.idea_box {
  display: flex;
  padding-left: calc(10rem);
  padding-right: calc(10rem);
  flex-direction: row;
  /* background: #efcd24; */
}

div.idea_bg {
  position: fixed;
  z-index: -1;
  height: calc(130rem);
  background: #efcd24;
  width: 0rem;
  right: calc(0rem);
  /* font-size: 50rem; */
}

div.idea_letter {
  padding-left: calc(8rem);
  padding-right: calc(8rem);
  font-family: FuturaLT-ExtraBold;
  /* font-weight: bolder; */
  text-transform: uppercase;
  font-size: calc(120rem);
  /* padding-top: calc(20rem); */
  display: inline-block;
  /* width: calc(100rem); */
  height: calc(130rem);
  display: flex;
  align-items: center;
  justify-content: center;
}

div.check_box {
  position: relative;
  top: calc(25rem);
  font-weight: normal;
  text-transform: uppercase;
  font-size: calc(100rem);
}

div.guide_part {
  position: fixed;
  bottom: calc(60rem);
  left: 50%;
  transform: translate(-50%, 0);
}

div.guide_box {
  /* background: #f2f2f5; */
  height: calc(80rem);
  width: calc(10rem);
  border-radius: calc(10rem);
}

div.guide_ball {
  position: relative;
  background: #efcd24;
  height: calc(10rem);
  width: calc(10rem);
  border-radius: calc(10rem);
}
</style>

<script>
import global from "../global";
import anime from "../../../modules/anime";
import Logo from "../../components/logo/Logo";

let idea_anime_end = false;
let guide_anime;

//export default
export default {
  name: "Home",
  data() {
    return {
      global: global,
      idea: [],
    };
  },
  components: {
    Logo,
  },
  beforeCreate() {
    //set documentElement root size and language package
    global.change_root_size();
    global.set_lang_pack();
  },
  created() {},
  mounted() {
    // mount the window size listen
    window.onresize = () => {
      return (() => {
        global.change_root_size();
      })();
    };
    // console.log(Logo.methods)
    Logo.methods.change_color("#251714", "#efcd24", 0);
    this.refresh_root_size();
    this.start_idea_anime();
    this.start_guide_anime();
  },
  destroyed() {
    idea_anime_end = true;
  },
  watch: {
    // watch the size change to refresh
    "global.width": function (val, oldVal) {
      this.refresh_root_size();
    },
  },
  methods: {
    refresh_root_size() {
      let static_piexl = 700;
      if (global.width < static_piexl) {
        document.documentElement.style.fontSize = static_piexl / 1920 + "px";
        return;
      }
      document.documentElement.style.fontSize = global.width / 1920 + "px";
    },
    start_idea_anime() {
      let that = this;
      let idx = 0;
      let idea_list = this.global.lang_pack.home_idea_list;
      idea_anime_end = false;
      let change_fun = function () {
        let idea = idea_list[idx];
        idx++;
        idx %= 3;
        that.idea = [];
        document.getElementById("idea_bg").style.width = "0px";
        for (let i = 0; i < idea.length; i++) {
          that.idea.push({
            idx: i,
            letter: idea[i],
            visibility: "hidden",
          });
        }

        for (let i = 0; i < idea.length; i++) {
          setTimeout(() => {
            that.idea[i].visibility = "visible";
            if (i === idea.length - 1) {
              let bg_width = document.getElementById("idea_box").clientWidth;
              new anime({
                targets: "div.idea_bg",
                width: bg_width + "px",
                delay: 100,
                duration: idea.length * 100,
                easing: "linear",
              });
            }
          }, i * 150);
        }

        if (idea_anime_end === false) {
          setTimeout(() => {
            change_fun();
          }, idea.length * 250 + 200);
        }
      };

      change_fun();
    },
    start_guide_anime() {
      new anime({
        targets: "div.guide_ball",
        opacity: [
          { value: ["0%", "100%"], duration: 250 },
          { value: ["100%", "0%"], delay: 750, duration: 250 },
        ],
        scaleY: [
          { value: 2, duration: 250, delay: 250 },
          { value: 1, duration: 250 },
        ],
        scaleX: [
          { value: 0.5, duration: 250, delay: 250 },
          { value: 1, duration: 250 },
        ],
        translateY: [
          {
            value: "30rem",
            duration: 250,
            delay: 250,
          },
          {
            value: "70rem",
            duration: 250,
          },
        ],
        // direction: "alternate",
        endDelay: 250,
        loop: true,
        easing: "linear",
      });
    },
  },
};
</script>



